@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  background: rgb(143, 100, 105);
  background: linear-gradient(142deg, rgb(184, 135, 140) 0%, rgb(132, 89, 160) 29%, rgb(106, 117, 192) 54%, rgb(118, 180, 196) 100%);
  color: white;
  font-family: "Roboto Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 1, "wght" 500, "GRAD" 200, "opsz" 48;
}

.circle {
  margin: 1rem 0;
}
.circle a {
  border: 2px solid whitesmoke;
  transition: all 0.2s ease;
  box-shadow: 0 0 0 0 #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.circle a:hover {
  box-shadow: -6px 6px 0 0 #fff;
  transform: translate(6px, -6px);
}
.circle img {
  z-index: -1;
  width: 20px;
  filter: invert(1);
}

.main-btn {
  border: 2px solid whitesmoke;
  transition: all 0.2s ease;
  box-shadow: 0 0 0 0 #fff;
  padding: 0.75rem 2.5rem;
  border-radius: 10px;
}
.main-btn:hover {
  box-shadow: -6px 6px 0 0 #fff;
  transform: translate(6px, -6px);
}

.BackgroundObject {
  z-index: -999;
}

.Modal-scroll {
  height: calc(100% - 2rem);
  scrollbar-gutter: stable;
}
.Modal-scroll::-webkit-scrollbar {
  width: 8px;
  margin: 10px;
}
.Modal-scroll::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 100px;
}
.Modal-scroll::-webkit-scrollbar-thumb {
  background-color: #e38ceb;
  border-radius: 100px;
}

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block; /* If you want dots under the hoverable text */
  /* Show the tooltip text when you mouse over the tooltip container */
}
.tooltip .tooltiptext {
  font-size: 11px;
  visibility: hidden;
  width: 220px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  top: 0;
  left: 110%;
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}
.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent rgba(0, 0, 0, 0.5) transparent transparent;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}/*# sourceMappingURL=style.css.map */